h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

address {
  font-style: inherit;
}

button {
  cursor: pointer;
  background-color: inherit;
  box-shadow: none;
  border: none;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

svg {
