  --text-color: #111321;
  --gray-color: #a8a8a8;
  --bg-color: #f4f4f4;
  --white-color: #fff;
  --footer-color: #7a7a7a;
  --switcher-gray-moon: #5f6775;
  --switcher-accent-day: #4b48db;
}

:root body.darkMode {
  --bg-color: #2e2e2e;
  --text-color: #f4f4f4;
  --switcher-accent-day: #f4f4f4;
  --switcher-gray-moon: #4440f7;
}

