.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  margin: 0;
  display: none;
}

.slider {
  cursor: pointer;
  border: .604701px solid var(--switcher-accent-day);
  background-color: var(--white-color);
  border-radius: 20px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: var(--switcher-accent-day);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  top: 50%;
  left: 1px;
  transform: translateY(-50%);
}

input:checked + .slider {
  background-color: var(--switcher-accent-day);
  border: 1px solid var(--white-color);
}

input:checked + .slider:before {
  background-color: var(--white-color);
  top: 50%;
  right: 1px;
  transform: translate(110%, -50%);
}

